import {Link} from "react-router-dom";
import * as MQTT from '../../js/mqtt'
// import {useEffect} from "react";

export default function list(){
    // 이중 for문으로 좌석 배치도 생성
    const row = [1,2];          // 행
    const col = [1,2,3,4,5];    // 열

        MQTT.MQTTSub();

    return(
        <main>
            <section className="gj do ir hj sp jr i pg text-blue-700 text-8xl">
                <div className="flex justify-center">
                {row.map((r,j)=> (
                    <div key={j} className="m-4">
                        {col.map((c,i) => (
                            c % 2 === 0 ?
                            <Link to='/podcast/list' state={{c}} key={i} className="p-10 rounded-3xl bg-red-500">{c * r}</Link> :
                            <Link to="/seat/list" key={i} className="p-10 rounded-3xl bg-blue-500">{c * r}</Link>
                        ))}
                    </div>
                ))}
                </div>
            </section>
        </main>
    )
}